@import '../../var';

.Account {
 &-Details {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  .Welcome {
   display: flex;
   align-items: center;
   .Role {
    margin-left: 35px;
    width: 400px; } } }
 &-Avatar {
  &Photo {
   object-fit: contain;
   width: 100%;
   max-height: 500px; } } }
.Avatar-Label {
 display: flex;
 flex-direction: column; }
.Label {
 cursor: pointer; }
#Avatar {
 display: none; }
.Button {
 color: $PC;
 background-color: transparent;
 border: none;
 margin: .5em 0; }
.Role {
 width: 100%;
 margin: 1em 0;
 padding: 10px 5px;
 text-align: center;
 color: #ffffff;
 background-color: $PC;
 text-transform: uppercase;
 font-weight: bold; }
.AdminRole {
 background-color: $PC; }
.AuthorRole {
 background-color: $PC; }
.UserRole {
 background-color: $PC; }
.Submit {
 &-Wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%; }
 &-Button {
  min-width: 150px;
  padding: 20px 35px;
  border-radius: 10px;
  background-color: $PC;
  font-weight: bold;
  text-transform: uppercase;
  color: #ffffff; } }
