@import '../components/var';
.Page {
 margin-left: 25vw;
 display: flex;
 min-height: 100vh;
 justify-content: center;
 align-items: center;
 .Creating-Post-Wrapper {
  width: 23vw; }
 .AdminPanel,&-Tags,&-About {
  min-height: 100vh; }
 &> div {
  width: 100%; } }
.Section {
 &-Wrapper {
  padding: 6em 1.8em; }
 &-Title {
  font-weight: 900;
  font-size: 34px;
  color: rgba(0,0,0,.8);
  margin-bottom: 30px;
  text-align: center;
  h2 {
   font-weight: inherit;
   color: inherit; } }
 &-Description {
  text-align: center;
  color: $PCG;
  margin-bottom: 6em;
  p {
   margin: 0 auto;
   max-width: 550px; } } }
.Handler-Loading {
 height: 100vh;
 width: 100%;
 display: flex;
 align-items: center;
 justify-self: center; }
.Background {
 &:before {
  content: '';
  position: absolute;
  z-index: 2;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.7); } }
